@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}

html{width:100%;height:100%;overflow:hidden}body{width:100%;height:100%;font-family:"Anonymous Pro", sans-serif;background-color:#121212}.main-container{width:100%;height:100%;color:#fff;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.typewriter-text{overflow:hidden;position:relative;width:18em;border-right:2px solid rgba(255,255,255,0.75);font-size:38px;white-space:nowrap;margin-top:100px;-webkit-animation:typewriter 2.7s steps(30) 1s 1 normal both,blinkTextCursor 300ms steps(30) infinite normal;animation:typewriter 2.7s steps(30) 1s 1 normal both,blinkTextCursor 300ms steps(30) infinite normal}@-webkit-keyframes typewriter{from{width:0}to{width:9em}}@keyframes typewriter{from{width:0}to{width:9em}}@-webkit-keyframes blinkTextCursor{from{border-right-color:rgba(255,255,255,0.75)}to{border-right-color:transparent}}@keyframes blinkTextCursor{from{border-right-color:rgba(255,255,255,0.75)}to{border-right-color:transparent}}

